* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  color: rgb(63, 68, 102);
}
.main-box {
  width: 900px;
  margin: 20px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.heading {
  width: 100%;
  margin-bottom: 8px;
  padding: 20px;
  background-color: rgb(100, 110, 182);
}
h1 {
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 34px;
  font-weight: 400;
  color: white;
  border-bottom: 1px solid white;
}
h1 + p {
  letter-spacing: 1px;
  font-size: 18px;
  font-weight: 300;
  color: white;
  margin: 5px 0;
}
h2 {
  width: 100%;
  color: white;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 2px;
  padding: 10px 20px;
  background-color: rgb(100, 110, 182);
}
.balance-sheet {
  width: 30%;
  background-color: rgb(235, 241, 253);
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
  align-content: center;
}
.balance-sheet > div {
  margin: 20px;
  display: flex;
  flex-flow: row wrap;
}
.balance-sheet > div > div {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin: 3px 0;
  padding: 0 0 0 10px;
}
.balance-sheet > div > div:first-child,
.balance-sheet > div > div:last-child {
  font-weight: 600;
  border-top: 1px solid rgb(129, 107, 196);
  border-bottom: 1px solid rgb(129, 107, 196);
  padding: 5px 0;
}
.balance-sheet button {
  padding: 5px 10px;
  color: white;
  background-color: rgb(129, 107, 196);
  border: none;
  border-radius: 3px;
}
.balance-sheet button:hover {
  background-color: rgb(63, 68, 102);
}
.balance-sheet > div:nth-child(3) {
  margin: 10px;
  display: flex;
  justify-content: center;
}
.balance-sheet > div:last-child,
.balance-sheet > div:last-child a {
  font-size: 9px;
  color: rgb(175, 171, 180);
  text-align: center;
}
.balance-sheet > div:last-child {
  justify-content: center;
  margin-top: 130px;
}
.income-statement-mainbox {
  width: 69%;
  display: flex;
  flex-flow: row wrap;
}
.income-statement-mainbox > div:first-child {
  margin-bottom: 10px;
}
.income-statement {
  width: 100%;
  background-color: rgb(235, 241, 253);
  display: flex;
  flex-flow: row wrap;
}
.income-statement > div:first-child {
  min-width: 25%;
  padding: 10px;
  background-color: rgb(149, 157, 207);
}
h3 {
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  width: 100%;
  color: white;
  border-bottom: 1px solid white;
  padding: 5px;
}
h3 + div {
  font-size: 30px;
  color: white;
  margin-top: 5px;
  text-align: center;
}
.income-statement-itemsbox {
  width: 75%;
  padding: 10px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
}
.income-statement-item {
  width: 120px;
  height: 130px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: center;
}
.income-statement-item > div {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}
.add-amt-form {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}
.add-amt-form p {
  font-size: 11px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: rgb(129, 107, 196);
}
.add-amt-form input {
  width: 100px;
  margin: 5px;
  padding: 2px 0 2px 10px;
  font-size: 12px;
  border: 1px solid rgb(149, 157, 207);
}
.add-amt-form input:focus {
  outline: 1px solid rgb(149, 157, 207);
}
.add-amt-form button img {
  width: 100%;
  overflow: hidden;
}
.income-statement-item button + div {
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  font-size: 10px;
  color: rgb(129, 107, 196);
}
.income-statement-item button + div + div {
  text-align: center;
  font-weight: 500;
  color: rgb(129, 107, 196);
  width: 90%;
  border-top: 1px solid rgb(100, 110, 182);
  padding: 3px;
  margin-top: 3px;
}
.itm-btn {
  width: 80px;
  height: 80px;
  font-size: 0;
  margin-bottom: 5px;
  object-fit: contain;
  overflow: hidden;
  border: 3px solid rgb(149, 157, 207);
  background-color: white;
}
.revenue {
  border-radius: 50% 0 50% 50%;
}
.expenses {
  border-radius: 50% 50% 50% 0;
}
.itm-btn img {
  width: 60%;
}
.itm-btn:hover {
  border-radius: 50%;
  border: 5px solid rgb(101, 71, 150);
}
.rmv-btn,
.add-amt-form button {
  font-size: 0;
  border: 2px solid white;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin: 3px;
  overflow: hidden;
  object-fit: cover;
}
.rmv-btn img {
  width: 100%;
}
.rmv-btn:hover,
.add-amt-form button:hover {
  border: 4px solid rgb(238, 255, 85);
}
